import { createSlice } from "@reduxjs/toolkit";
import { getItem } from "./helpers";

export const setupsSlice = createSlice({
  name: "setup",
  initialState: {
    chosenID: 0,
    mySetups: []
  },
  reducers: {
    UPLOAD_SETUPS: (state, action) => {
      state.mySetups = action.payload;
      for (const st of action.payload) {
        if (st.type !== "combine") {
          state.chosenID = st.ID;
          break;
        }
      }
    },
    CHOOSE_SETUP: (state, action) => {
      state.chosenID = action.payload;
    },
    REMOVE_SETUP: (state, action) => {
      const removedID = action.payload;
      const { mySetups } = state;
      const visibleIDs = mySetups.reduce((filtered, st) => {
        if (st.type !== "combined") {
          filtered.push(st.ID);
        }
        return filtered;
      }, []);
      const removedIndexInVisible = visibleIDs.indexOf(removedID);
      const lastIndex = visibleIDs.length - 1;
      const newID =
        removedIndexInVisible === lastIndex
          ? visibleIDs[lastIndex - 1] || 0
          : visibleIDs[removedIndexInVisible + 1];
      const removedIndexInAll = mySetups.findIndex((st) => st.ID === removedID);
      mySetups.splice(removedIndexInAll, 1);
      state.chosenID = newID;
    },
    COMBINE_SETUPS: (state, action) => {
      const { pickedIDs, name } = action.payload;
      const { mySetups } = state;
      const ID = Date.now();
      const allIDs = {};
      for (const id of pickedIDs) {
        const setup = getItem(mySetups, id);
        setup.type = "combined";
        allIDs[setup.char.name] = id;
      }
      mySetups.unshift({
        name,
        ID,
        type: "complex",
        shownID: pickedIDs[0],
        allIDs
      });
      state.chosenID = ID;
    },
    ADD_TO_COMPLEX: (state, action) => {
      const { charNames, indexes, complexID, pickedIDs } = action.payload;
      const { mySetups } = state;
      charNames.forEach((name, i) => {
        mySetups[indexes[i]].type = "combined";
        getItem(mySetups, complexID).allIDs[name] = pickedIDs[i];
      });
    },
    UNCOMBINE_COMPLEX: (state, action) => {
      const complexID = action.payload;
      const { mySetups } = state;
      const { allIDs } = getItem(mySetups, complexID);
      for (const member in allIDs) {
        getItem(state.mySetups, allIDs[member]).type = "original";
      }
      const index = state.mySetups.findIndex((st) => st.ID === complexID);
      state.mySetups.splice(index, 1);
    },
    SWITCH_SETUP_ON_COMPLEX: (state, action) => {
      const { complexID, newID } = action.payload;
      getItem(state.mySetups, complexID).shownID = newID;
    },
    SAVE_SETUP: (state, action) => {
      const { parentStI, ID, name, existedI, data } = action.payload;
      const isExisted = existedI >= 0;
      const { mySetups } = state;
      if (parentStI >= 0) {
        mySetups[parentStI].shownID = ID;
      }
      const newSetup = {
        name,
        ID,
        type: isExisted ? mySetups[existedI].type : "original",
        ...data
      };
      if (isExisted) mySetups[existedI] = newSetup;
      else mySetups.unshift(newSetup);
    }
  }
});

export const {
  UPLOAD_SETUPS,
  CHOOSE_SETUP,
  REMOVE_SETUP,
  COMBINE_SETUPS,
  ADD_TO_COMPLEX,
  UNCOMBINE_COMPLEX,
  SWITCH_SETUP_ON_COMPLEX,
  SAVE_SETUP
} = setupsSlice.actions;

export default setupsSlice.reducer;
