import { useDispatch, useSelector } from "react-redux";
import getBaseStats from "../../../calculators/baseStats";
import characters from "../../../data/characters";
import { getArtSets } from "../../../helpers/processors";
import { CHANGE_TALENT, TOGGLE_CONS } from "../../../redux/databaseSlice";
import { Card } from "../../../styledCpns/Database";
import { Box } from "../../../styledCpns/Layout";
import ConsList from "../../Readers/ConsList";
import TalentList from "../../Readers/TalentList";
import SectionAttr from "./SectionAttr";
import SectionGears from "./SectionGears";

export default function Info({ charIndex, setCharIndex }) {
  const { myChars, myWps, myArts } = useSelector((state) => state.database);
  const { weaponID, artIDs, ...char } = myChars[charIndex];
  const { name } = char;
  const charData = characters[name];
  const weapon = myWps.find((wp) => wp.ID === weaponID);
  const art = getArtifact(artIDs, myArts);
  const [ATTRs, artSBnes] = getBaseStats(charData, char, weapon, art);
  const dispatch = useDispatch();

  return (
    <Box
      className="py-3 flex overflow-auto"
      width={window.innerWidth <= 480 ? "calc(100% - 2rem)" : "88%"}
      height="98%"
    >
      <SectionAttr
        charData={charData}
        char={char}
        ATTRs={ATTRs}
        setCharIndex={setCharIndex}
      />
      <SectionGears weapon={weapon} art={art} artSBnes={artSBnes} />
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <ConsList
            char={char}
            handleClick={(consIndex) =>
              dispatch(TOGGLE_CONS({ name, consIndex }))
            }
          />
        </Box>
      </Card>
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <TalentList
            char={char}
            upgradeTl={(type, level) =>
              dispatch(CHANGE_TALENT({ name, type, level }))
            }
          />
        </Box>
      </Card>
    </Box>
  );
}

function getArtifact(artIDs, myArts) {
  const pieces = artIDs.map((id) => myArts.find((p) => p.ID === id) || null);
  return { pieces, sets: getArtSets(pieces) };
}
