import { getXtraTalentLv } from "../../../../calculators/helpers";
import { Green, LGreen } from "../../../../styledCpns/DataDisplay";
import Ayato from "./index";

export const buffs = [
  {
    index: 0,
    src: "Elemental Skill",
    desc: () => (
      <>
         Causes {Ayato.actvTalents[1].desc[1].lines[0]}
        <br /> Namisen increases {Ayato.actvTalents[1].desc[1].lines[1]}
      </>
    ),
    isGranted: () => true,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    index: 1,
    src: "Elemental Burst",
    desc: ({ toSelf, char, party, inputs }) => (
      <>
        Increases the <LGreen>Normal Attack DMG</LGreen> of characters within
        its AoE by{" "}
        <Green>{Ayato.buffs[1].bnValue(toSelf, char, party, inputs)}%</Green>.
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Burst Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [15],
    addBnes: ({ bonuses, toSelf, char, party, inputs }) => {
      bonuses.NA.DMG += Ayato.buffs[1].bnValue(toSelf, char, party, inputs);
    },
    bnValue: (toSelf, char, party, inputs) => {
      const level = toSelf
        ? char["Elemental Burst"] +
          getXtraTalentLv(Ayato.actvTalents[2], char, party)
        : inputs[0];
      return level ? Math.min(level + 10, 20) : 0;
    }
  },
  {
    index: 2,
    outdated: true,
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        <LGreen>Kamisato Art: Suiyuu's DMG</LGreen> is increased based on{" "}
        <Green>3%</Green> of Ayato's <LGreen>Max HP</LGreen>.
      </>
    )
    // <Red>DMG Bonus (base): {Ayato.buffs[2].bnValue(ATTRs)}.</Red>
    // isGranted: (char) => char.ascension >= 4,
    // affect: "self",
    // addFinalBnes: ({ bonuses, ATTRs }) => {
    //   bonuses.EB.Flat += Ayato.buffs[2].bnValue(ATTRs);
    // },
    // bnValue: (ATTRs) => Math.round((ATTRs.HP * 3) / 100)
  },
  {
    index: 3,
    src: "Constellation 1",
    desc: () => Ayato.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    index: 4,
    src: "Constellation 2",
    desc: () => Ayato.constellation[1].buff,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["HP%"] += 40;
    }
  },
  {
    index: 5,
    src: "Constellation 4",
    desc: () => Ayato.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 20;
    }
  }
];
