import { useCallback, useState } from "react";
import { useDispatch } from "react-redux";
import { artTypes } from "../../../../data/misc";
import { useGetHeight } from "../../../../helpers/hooks";
import { SWITCH_ART, SWITCH_WP } from "../../../../redux/databaseSlice";
import { Card } from "../../../../styledCpns/Database";
import { Box, HiddenSpace } from "../../../../styledCpns/Layout";
import { CloseBtn } from "../../../CommonCpns";
import ArtInventory from "../../../Readers/ArtInventory";
import { SharedSpace } from "../../../Readers/styles";
import WpInventory from "../../../Readers/WpInventory";
import Details from "./Details";
import Overview from "./Overview";

export default function PreCheck({ weapon, art, artSBnes }) {
  const [opened, setOpened] = useState(-1);
  const [showing, setShowing] = useState(false);
  const [wpInvOn, setWpInvOn] = useState(false);
  const [artInvI, setArtInvI] = useState(-1);
  const dispatch = useDispatch();
  const owner = weapon.user;

  const toggleDetails = useCallback(
    (type) => {
      if (opened === type) {
        setShowing(false);
        setTimeout(() => setOpened(-1), 200);
      } else {
        setShowing(true);
        setOpened(type);
      }
    },
    [opened]
  );
  const [ref, height] = useGetHeight();

  const overview = (
    <Overview
      weapon={weapon}
      art={art}
      opened={opened}
      toggleDetails={toggleDetails}
      setArtInvI={setArtInvI}
    />
  );

  const details = (
    <Details
      large={window.innerWidth >= 686}
      height={height}
      opened={opened}
      setOpened={setOpened}
      weapon={weapon}
      setWpInvOn={setWpInvOn}
      art={art}
      openArtInv={() => setArtInvI(opened)}
      artSBnes={artSBnes}
      setShowing={setShowing}
      close={() => toggleDetails(opened)}
    />
  );

  return (
    <div ref={ref} className="ml-2">
      {window.innerWidth < 686 ? (
        <SectionGearsSmall
          opened={opened}
          showing={showing}
          closeDetails={() => toggleDetails(opened)}
          overview={overview}
          details={details}
        />
      ) : (
        <SectionGears
          opened={opened}
          showing={showing}
          overview={overview}
          details={details}
        />
      )}
      {wpInvOn && (
        <WpInventory
          owner={owner}
          wpType={weapon.type}
          close={() => setWpInvOn(false)}
          btnText="Switch"
          btnClick={({ user, ID }) =>
            dispatch(
              SWITCH_WP({
                newUser: user,
                newID: ID,
                oldUser: owner,
                oldID: weapon.ID
              })
            )
          }
        />
      )}
      {artInvI >= 0 && (
        <ArtInventory
          curPieces={art.pieces}
          artType={artTypes[artInvI]}
          owner={owner}
          close={() => setArtInvI(-1)}
          btnText="Switch"
          btnClick={({ user, ID }) =>
            dispatch(
              SWITCH_ART({
                newUser: user,
                newID: ID,
                oldUser: owner,
                oldID: art.pieces[artInvI]?.ID,
                pos: artInvI
              })
            )
          }
        />
      )}
    </div>
  );
}

function SectionGearsSmall({
  opened,
  showing,
  closeDetails,
  overview,
  details
}) {
  return (
    <Card className="full-h" width="20.75rem">
      <Box className="full-h">
        <SharedSpace>
          <div className={"left_part " + (showing ? "close" : "open")}>
            {overview}
          </div>
          <div className={"right_part " + (showing ? "open" : "close")}>
            <div className="full-h flex-col">
              <CloseBtn className="mx-auto" onClick={closeDetails} />
              {opened !== -1 && details}
            </div>
          </div>
        </SharedSpace>
      </Box>
    </Card>
  );
}

function SectionGears({ opened, showing, overview, details }) {
  return (
    <div className="full-h flex">
      <Card width="20.75rem">{overview}</Card>
      <HiddenSpace
        className={"hide-sb " + (showing ? "show" : "")}
        openWidth="20.375rem"
      >
        {opened !== -1 && details}
      </HiddenSpace>
    </div>
  );
}
