import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import characters from "../../data/characters";
import { deepCopy } from "../../helpers/misc";
import { getFinalInfusion } from "../../helpers/processors";
import { PREPARE_IMPORT } from "../../redux/calculatorSlice";
import { StyledInv } from "../../styledCpns/Database";
import { LGreen, Red, Text } from "../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import { HowToModal, NoItems } from "../CommonCpns";
import { AddCtrl, CombineCtrl, RemoveCtrl, ShareCtrl } from "./Controllers";
import DmgResults from "./DmgResults";
import { createTmSetup, scroll } from "./helpers";
import Modal from "./Modal";
import Setup from "./Setup";

export default function MySetups() {
  const { myChars, myWps, myArts } = useSelector((state) => state.database);
  const { chosenID, mySetups } = useSelector((state) => state.setups);
  const [infoOn, setInfoOn] = useState(false);
  const [modalType, setModalType] = useState(null);
  const [removedID, setRemovedID] = useState(null);
  const [sharedID, setSharedID] = useState(null);
  const [combining, setCombining] = useState(false);
  const [addingID, setAddingID] = useState(null);
  const dispatch = useDispatch();
  const ref = useRef();

  let chosenSt = mySetups.find((st) => st.ID === chosenID);
  if (chosenSt && chosenSt.type === "complex") {
    chosenSt = mySetups.find((st) => st.ID === chosenSt.shownID);
  }
  useEffect(() => {
    if (chosenID) scroll(ref, mySetups, chosenID);
  }, [chosenID]);

  const buildTmSt = (setup, tmI) => {
    dispatch(PREPARE_IMPORT(createTmSetup(setup, tmI, myChars, myWps, myArts)));
  };

  let infusion;
  let ATTRs;
  let artSBnes;
  let rxnBnes;
  let dmg;

  if (chosenSt) {
    const {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      customMCs = { BCs: [], DCs: [] },
      party,
      target
    } = chosenSt;
    const charData = characters[char.name];
    infusion = getFinalInfusion(char, selfMCs.BCs, charData.vision, party);
    let bonuses;
    [ATTRs, bonuses, rxnBnes, artSBnes] = getBuffedStats(
      charData,
      char,
      selfMCs.BCs,
      weapon,
      subWpMCs.BCs,
      art,
      elmtMCs.resonance,
      party,
      customMCs.BCs,
      infusion
    );
    dmg = getDmg(
      deepCopy(char),
      selfMCs,
      party,
      art.subDCs,
      ATTRs,
      bonuses,
      rxnBnes,
      customMCs.DCs,
      infusion,
      elmtMCs,
      target
    );
  }
  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <Box className="btn_bar" height="2.5rem">
          <IconBtn
            className="mr-3 glow-on-hover"
            icon="fas fa-info"
            variant="positive"
            size="1.625rem"
            fontSize="0.875rem"
            onClick={() => setInfoOn(true)}
          />
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => setCombining(true)}
          >
            Combine
          </Button>
        </Box>
        <Box className="body" height="35rem">
          <Box
            ref={ref}
            className="pl-2 pt-2 flex-col align-start overflow-auto scroll-smooth"
            width="40.5rem"
            minWidth="40.5rem"
          >
            {mySetups.length ? (
              mySetups.map((setup) => {
                const { ID } = setup;
                if (setup.type === "original") {
                  return (
                    <Setup
                      key={ID}
                      ID={ID}
                      setup={setup}
                      setModalType={setModalType}
                      setSharedID={setSharedID}
                      buildTmSt={buildTmSt}
                      setRemovedID={setRemovedID}
                    />
                  );
                } else if (setup.type === "complex") {
                  const shownSetup = mySetups.find(
                    (st) => st.ID === setup.shownID
                  );
                  return (
                    <Setup
                      key={ID}
                      ID={ID}
                      setup={shownSetup}
                      stName={setup.name}
                      setModalType={setModalType}
                      setSharedID={setSharedID}
                      buildTmSt={buildTmSt}
                      setAddingID={setAddingID}
                      allIDs={setup.allIDs}
                    />
                  );
                }
                return null;
              })
            ) : (
              <div className="full-w">
                <NoItems type="Setups" />
              </div>
            )}
          </Box>
          <DmgResults setup={chosenSt} dmg={dmg} />
        </Box>
      </StyledInv>
      {modalType && (
        <Modal
          type={modalType}
          result={{ infusion, ATTRs, artSBnes, rxnBnes }}
          setup={chosenSt}
          close={() => setModalType(null)}
        />
      )}
      {removedID && (
        <RemoveCtrl removedID={removedID} close={() => setRemovedID(null)} />
      )}
      {sharedID && (
        <ShareCtrl
          setup={mySetups[mySetups.findIndex((st) => st.ID === sharedID)]}
          close={() => setSharedID(null)}
        />
      )}
      {combining && (
        <CombineCtrl setups={mySetups} close={() => setCombining(false)} />
      )}
      {addingID && (
        <AddCtrl
          setups={mySetups}
          addingID={addingID}
          close={() => setAddingID(false)}
        />
      )}
      {infoOn && <Info close={() => setInfoOn(false)} />}
    </Box>
  );
}

function Info({ close }) {
  return (
    <HowToModal
      content={
        <div>
          <Text>
            - <LGreen>Modify Setups</LGreen>: When you press the wrench button
            on a Setup, you're pushing a <Red>copy</Red> of it to the
            Calculator, so don't forget to save the modified copy if you want to
            apply the changes to that Setup.
          </Text>
          <Text className="mt-2">
            - <LGreen>Complex Setup</LGreen>: It is the result of combining
            Setups. On this complex, switch to teammates' Setups by pressing
            their icons. Break the complex into individual Setups again by
            pressing the link / chain icon before its name.
          </Text>
          <Text className="mt-2">
            - You cannot change teammates when modifying a Setup that is in a
            complex.
          </Text>
          <Text className="mt-2">
            - You can build <LGreen>teammate's Setups</LGreen> based on a saved
            Setup by hovering over teammate's icon and press the calculator
            button that would appear. Party members and Target will be the same.
            Some Modifiers will remain activated.
          </Text>
        </div>
      }
      close={close}
    />
  );
}
