import { useContext } from "react";
import { useSwitcher } from "../../../helpers/hooks";
import { deepCopy } from "../../../helpers/misc";
import { getThisChar } from "../../../helpers/processors";
import { SetBonus } from "../../CommonCpns";
import ConsList from "../../Readers/ConsList";
import StatsTable from "../../Readers/StatsTable";
import { SharedSpace } from "../../Readers/styles";
import TalentList from "../../Readers/TalentList";
import WeaponCard from "../../Readers/WeaponCard";
import {
  ArtChestCt,
  CharCt,
  FixedCts,
  PartiesCt,
  ResultCt,
  SetupCt,
  WpRackCt
} from "../index";

export default function TabContent({ tab }) {
  let content;
  if (tab === "Attributes") {
    content = <Attributes />;
  } else if (tab === "Weapon") {
    content = <Weapon />;
  } else if (tab === "Artifacts") {
    content = <Artifacts />;
  } else if (tab === "Constellation") {
    content = <Constellation />;
  } else {
    content = <Talents />;
  }
  return <div className="mt-2 pt-1 grow-1 hide-sb">{content}</div>;
}

function Attributes() {
  const { allATTRs } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  return (
    <div className="full-h custom-sb">
      <StatsTable stats={allATTRs[curSetupI]} breakdown={true} />
    </div>
  );
}

function Weapon() {
  const wpRack = useContext(WpRackCt);
  const curSetupI = useContext(SetupCt);
  const { setWpRack } = useContext(FixedCts);
  return (
    <div className="full-h hide-sb">
      <WeaponCard
        weapon={wpRack[curSetupI]}
        mutable={true}
        upgrade={(level) =>
          setWpRack((prev) => {
            const rack = deepCopy(prev);
            rack[curSetupI].level = level;
            return rack;
          })
        }
        refine={(rfm) =>
          setWpRack((prev) => {
            const rack = deepCopy(prev);
            rack[curSetupI].refinement = +rfm;
            return rack;
          })
        }
      />
    </div>
  );
}

function Artifacts() {
  const artChest = useContext(ArtChestCt);
  const { allArtSBnes } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  const [switcher, tab] = useSwitcher([
    { text: "Details", clickable: true },
    { text: "Set Bonus", clickable: true }
  ]);
  return (
    <div className="full-h flex-col">
      <SharedSpace>
        <div className={"left_part " + (tab === "Details" ? "open" : "close")}>
          <div className="full-h custom-sb">
            <StatsTable stats={allArtSBnes[curSetupI]} breakdown={false} />
          </div>
        </div>
        <div
          className={"right_part " + (tab === "Set Bonus" ? "open" : "close")}
        >
          <div className="full-h hide-sb">
            <SetBonus sets={artChest[curSetupI].sets} />
          </div>
        </div>
      </SharedSpace>
      <div className="mt-3">{switcher}</div>
    </div>
  );
}

function Constellation() {
  const { name, constellation: cons } = useContext(CharCt);
  const { setChar } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const sepCons = Array.isArray(cons);
  const constellation = sepCons ? cons[curSetupI] : cons;
  return (
    <ConsList
      char={{ name, constellation }}
      handleClick={(i) => {
        setChar((prev) => {
          const newChar = deepCopy(prev);
          const newCons = constellation === i + 1 ? i : i + 1;
          if (sepCons) {
            newChar.constellation[curSetupI] = newCons;
          } else {
            newChar.constellation = newCons;
          }
          return newChar;
        });
      }}
    />
  );
}

function Talents() {
  const parties = useContext(PartiesCt);
  const char = useContext(CharCt);
  const { setChar } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const thisChar = getThisChar(char, curSetupI);
  return (
    <TalentList
      char={thisChar}
      party={parties[curSetupI]}
      upgradeTl={(tlType, lv) =>
        setChar((prev) => {
          const newChar = deepCopy(prev);
          if (Array.isArray(newChar[tlType])) {
            newChar[tlType][curSetupI] = lv;
          } else {
            newChar[tlType] = lv;
          }
          return newChar;
        })
      }
    />
  );
}
