import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import characters from "../../data/characters";
import { wikia } from "../../data/misc";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import CharSorting from "./CharSorting";

export default function TopBar({
  charIndex,
  setCharIndex,
  setPickerOn,
  charListRef
}) {
  const [sorting, setSorting] = useState(false);
  const [gridview, setGridview] = useState(false);
  const { myChars } = useSelector((state) => state.database);

  useEffect(() => {
    const thisCpn = charListRef.current;
    const scrollHorizontally = (e) => {
      const delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));
      thisCpn.scrollLeft -= delta * 50;
      e.preventDefault();
    };
    thisCpn.addEventListener("wheel", scrollHorizontally);
    return () => thisCpn.removeEventListener("wheel", scrollHorizontally);
  }, [charListRef]);

  return (
    <Box className="full-w flex justify-center" bgColor="cardBg2">
      <StyledDiv>
        <div ref={charListRef} className="mt-2 flex full-w hide-sb">
          {myChars.length ? (
            myChars.map(({ name }, i) => (
              <IconBox
                key={name}
                chosen={charIndex === i}
                choose={() => setCharIndex(i)}
                name={name}
              />
            ))
          ) : (
            <Box
              className="full-w flex justify-end align-center"
              height="4.875rem"
            >
              <Text variant="h2" color="lightGold">
                Add New Characters {">>"}
              </Text>
            </Box>
          )}
        </div>
        {Boolean(myChars.length) && (
          <AbsoluteBox top="2rem" right="100%" className="flex">
            <IconBtn
              className="mr-3 glow-on-hover"
              icon="fas fa-sort"
              variant="positive"
              fontSize="1.25rem"
              onClick={() => setSorting(true)}
            />
            <IconBtn
              icon="fas fa-th"
              className="mr-3 glow-on-hover"
              variant="positive"
              onClick={() => setGridview(true)}
            />
          </AbsoluteBox>
        )}
        <img
          src={wikia + "4/48/System_Wish.png"}
          alt="wish"
          className="wish_btn pointer"
          draggable={false}
          onClick={() => setPickerOn(true)}
        />
      </StyledDiv>
      {sorting && <CharSorting close={() => setSorting(false)} />}
      {gridview && (
        <CharPicker
          setCharIndex={setCharIndex}
          charListRef={charListRef}
          close={() => setGridview(false)}
        />
      )}
    </Box>
  );
}

function IconBox({ chosen, choose, name }) {
  const { beta, icon, sideIcon } = characters[name];
  const Wrapper = beta ? BetaWrapper : SideWrapper;
  const xtraClasses = beta ? "m-1 overflow-hidden" : "m-2";
  return (
    <div
      className={"mx-1 cell pointer" + (chosen ? " chosen" : "")}
      onClick={choose}
    >
      <Wrapper className={"icon_wrapper " + xtraClasses}>
        <img
          src={wikia + (beta ? icon : sideIcon) + ".png"}
          alt="icon"
          draggable={false}
        />
      </Wrapper>
    </div>
  );
}

function CharPicker({ setCharIndex, charListRef, close }) {
  const { myChars } = useSelector((state) => state.database);
  const data = myChars.map((char) => {
    const { name, constellation } = char;
    const { code, icon, rarity, vision, weapon } = characters[name];
    return { code, name, constellation, icon, rarity, vision, weapon };
  });
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        const index = myChars.findIndex((char) => char.name === name);
        setCharIndex(index);
        if (charListRef.current) charListRef.current.scrollLeft = index * 84;
      }}
    />
  );
}

const StyledDiv = styled.div`
  position: relative;
  width: 70%;
  max-width: 60%;
  .cell {
    border-bottom: 3px solid transparent;
  }
  .cell.chosen {
    border-bottom-color: rgb(92, 188, 212);
  }
  .icon_wrapper {
    border-radius: 50%;
    border: 3.5px solid rgba(185, 185, 185, 0.3);
    background: rgba(0, 0, 0, 0.3);
  }
  .cell.chosen > .icon_wrapper {
    border-color: rgb(92, 188, 212);
    background-color: rgb(92, 188, 212);
  }
  .wish_btn {
    position: absolute;
    top: 1rem;
    left: calc(100% + 1.5rem);
    width: 60px;
    border-radius: 50%;
  }
  .wish_btn:hover {
    box-shadow: 0 0 3px 3px #fff;
  }
  @media (min-width: 768px) {
    max-width: 70%;
  }
`;

const SideWrapper = styled.div`
  width: 60px;
  min-width: 60px;
  height: 60px;
  & > img {
    width: 85px;
    transform: translate(-15px, -32px);
  }
`;

const BetaWrapper = styled.div`
  width: 68px;
  min-width: 68px;
  height: 68px;
  & > img {
    width: 65px;
    transform: translate(-2.5px, -0px);
  }
`;
