import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../data/characters";
import { useGetHeight } from "../../helpers/hooks";
import { findWeapon } from "../../helpers/misc";
import { REMOVE_WP, SWAP_WP_USER } from "../../redux/databaseSlice";
import { Card } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, EquippedChar } from "../CommonCpns";
import Picker from "../Picker";
import { MyWpCard } from "../Readers/MyItemCard";

export default function RightSection({ weapon, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(null);
  const [removing, setRemoving] = useState(false);
  const [ref, height] = useGetHeight();
  return (
    <div ref={ref} className="full-h flex-col">
      <div className="grow-1 flex align-start">
        <Card
          className="flex-col"
          style={{ minHeight: "27rem", maxHeight: height / 16 - 3 + "rem" }}
        >
          <Box className="grow-1 hide-sb" width="18.75rem">
            {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
          </Box>
          {weapon ? (
            <ButtonBar
              className="mt-3"
              texts={["Remove", "Equip"]}
              handlers={[() => setRemoving(true), () => setPickerOn(true)]}
            />
          ) : null}
        </Card>
      </div>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
      {pickerOn && (
        <CharPicker weapon={weapon} close={() => setPickerOn(false)} />
      )}
      {removing && (
        <ConfirmRemove
          weapon={weapon}
          setChosenID={setChosenID}
          close={() => setRemoving(false)}
        />
      )}
    </div>
  );
}

function CharPicker({ weapon: { ID, user, type }, close }) {
  const { myChars } = useSelector((state) => state.database);
  const data = [];
  for (let { name, constellation } of myChars) {
    const { code, icon, rarity, vision, weapon } = characters[name];
    if (weapon === type && name !== user) {
      data.push({ code, name, icon, rarity, vision, weapon, constellation });
    }
  }
  const dispatch = useDispatch();
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) =>
        dispatch(SWAP_WP_USER({ newUser: name, wpID: ID, oldUser: user }))
      }
    />
  );
}

function ConfirmRemove({
  weapon: { ID, user, code, type },
  setChosenID,
  close
}) {
  const dispatch = useDispatch();
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{findWeapon({ type, code }).name}</b>
          "?{" "}
          {Boolean(user) && (
            <>
              It is currently used by <b>{user}</b>.
            </>
          )}
        </>
      }
      close={close}
      confirm={() => dispatch(REMOVE_WP({ ID, user, type, setChosenID }))}
    />
  );
}
