import { createSlice } from "@reduxjs/toolkit";

export const setupsSlice = createSlice({
  name: "ui",
  initialState: {
    tab: "Calculator"
  },
  reducers: {
    CHANGE_TAB: (state, action) => {
      state.tab = action.payload;
    }
  }
});

export const { CHANGE_TAB } = setupsSlice.actions;

export default setupsSlice.reducer;
