import { useContext, useState } from "react";
import { useCloseModal } from "../../../../helpers/hooks";
import { deepCopy, pctOf } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";
import { Text } from "../../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../../styledCpns/Inputs";
import { Box, ModalCarpet } from "../../../../styledCpns/Layout";
import { ButtonBar } from "../../../CommonCpns";
import { CustomMCsCt, FixedCts, ManagerCts, SetupCt } from "../../index";
import { CopySection } from "../../styles";
import { buffCats, buffTypes, debuffTypes, Input, TabBar } from "./Children";

export default function CustomMods({ modType }) {
  const { setCustomMCs } = useContext(FixedCts);
  const customMCs = useContext(CustomMCsCt);
  const curSetupI = useContext(SetupCt);
  const [forgeOn, setForgeOn] = useState(false);
  const mods = customMCs[curSetupI][modType];
  const { setups } = useContext(ManagerCts);

  const copyOpts = [];
  if (!mods.length) {
    for (const i in customMCs) {
      if (customMCs[i][modType].length) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <div className="flex-col">
      <div className="mx-auto mt-3 flex">
        {mods.length > 0 && (
          <IconBtn
            icon="fas fa-trash-alt"
            className="mr-5 glow-on-hover"
            variant="negative"
            onClick={() =>
              setCustomMCs((prev) => {
                const MCs = deepCopy(prev);
                MCs[curSetupI][modType] = [];
                return MCs;
              })
            }
          />
        )}
        {mods.length <= 10 && (
          <IconBtn
            icon="fas fa-plus"
            className="glow-on-hover"
            variant="positive"
            onClick={() => setForgeOn(true)}
          />
        )}
      </div>
      {copyOpts.length ? (
        <div className="mt-5">
          <CopySection
            options={copyOpts}
            copy={(stName) =>
              setCustomMCs((prev) => {
                const MCs = deepCopy(prev);
                const stIndex = setups.findIndex(({ name }) => name === stName);
                MCs[curSetupI][modType] = deepCopy(MCs[stIndex][modType]);
                return MCs;
              })
            }
          />
        </div>
      ) : null}
      {mods.map(({ type, val }, i) => (
        <div key={i} className="mt-4 flex align-center">
          <IconBtn
            icon="fas fa-minus"
            className="mr-3 glow-on-hover"
            variant="negative"
            fontSize="1.5rem"
            onClick={() =>
              setCustomMCs((prev) => {
                const MCs = deepCopy(prev);
                MCs[curSetupI][modType].splice(i, 1);
                return MCs;
              })
            }
          />
          <div className="grow-1 flex align-center">
            <Text className="pr-2">{type}</Text>
            <Input
              className="ml-auto p-2"
              value={val}
              onChange={(e) =>
                setCustomMCs((prev) => {
                  const MCs = deepCopy(prev);
                  MCs[curSetupI][modType][i].val = processNumInp(
                    e.target.value,
                    val,
                    999
                  );
                  return MCs;
                })
              }
            />
          </div>
        </div>
      ))}
      {forgeOn && <Forge modType={modType} close={() => setForgeOn(false)} />}
    </div>
  );
}

function initState(forBCs) {
  const state = { type: forBCs ? "ATK%" : "DEF reduction", val: 0 };
  if (forBCs) {
    state.cat = "Attributes";
  }
  return state;
}

function Forge({ modType, close }) {
  const { setCustomMCs } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const forBCs = modType === "BCs";
  const [state, setState] = useState(initState(forBCs));
  const { cat, type, val } = state;
  const options = forBCs ? buffTypes[cat] : debuffTypes;

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 flex-col white-glow-shadow max-width-95"
        bgColor="cardBg1"
        borderRadius="0.5rem"
      >
        {forBCs && <TabBar state={state} setState={setState} />}
        <div
          className={(forBCs ? "mt-5" : "mt-3") + " mx-auto flex align-center"}
        >
          <Select
            className="pr-2"
            color="white"
            value={type}
            onChange={(e) =>
              setState((prev) => ({ ...prev, type: e.target.value }))
            }
          >
            {options.map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
          <Input
            className="ml-3 p-2"
            value={val}
            onChange={(e) =>
              setState((prev) => ({
                ...prev,
                val: processNumInp(e.target.value, val, 999)
              }))
            }
          />
          <Text className="ml-2">
            {!forBCs || ["Talents", "Reactions"].includes(cat)
              ? "%"
              : pctOf(type)}
          </Text>
        </div>
        <ButtonBar
          className="mt-5"
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              setCustomMCs((prev) => {
                const MCs = deepCopy(prev);
                const newC = { type, val };
                if (forBCs) {
                  newC.catKey = buffCats.indexOf(cat);
                }
                MCs[curSetupI][modType].unshift(newC);
                return MCs;
              });
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}
