import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useCloseModal } from "../../helpers/hooks";
import {
  ADD_TO_COMPLEX,
  COMBINE_SETUPS,
  REMOVE_SETUP
} from "../../redux/setupsSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { TextInput } from "../../styledCpns/Inputs";
import { ModalCarpet } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, ShareLayout } from "../CommonCpns";
import { Layout, Menu } from "./CombineLayout";

export function RemoveCtrl({ removedID, close }) {
  const { mySetups } = useSelector((state) => state.setups);
  const { name } = mySetups.find((st) => st.ID === removedID);
  const dispatch = useDispatch();
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{name}</b>"?
        </>
      }
      confirm={() => {
        dispatch(REMOVE_SETUP(removedID));
      }}
      close={close}
    />
  );
}

export function ShareCtrl({ setup: { name, ID, type, ...data }, close }) {
  return <ShareLayout setupName={name} data={data} close={close} />;
}

export function CombineCtrl(props) {
  const { setups, close } = props;
  const [pickedIDs, setPickedIDs] = useState([]);
  const [inp, setInp] = useState("Team Setup");
  const [err, setErr] = useState(false);

  const dispatch = useDispatch();
  useEffect(() => setErr(false), [pickedIDs]);

  const tryCombine = () => {
    try {
      if (!inp) throw new Error("1");
      const mains = [];
      const all = [];
      for (const ID of pickedIDs) {
        const {
          char: { name },
          party
        } = setups[setups.findIndex((st) => st.ID === ID)];
        if (mains.includes(name)) {
          throw new Error();
        } else {
          mains.push(name);
        }
        if (!all.includes(name)) {
          if (all.length === 4) {
            throw new Error();
          } else {
            all.push(name);
          }
        }
        for (let tm of party) {
          if (tm.name && !all.includes(tm.name)) {
            if (all.length === 4) {
              throw new Error();
            } else {
              all.push(tm.name);
            }
          }
        }
      }
      dispatch(COMBINE_SETUPS({ pickedIDs, name: inp }));
      close();
    } catch (e) {
      if (e.message !== "1") setErr(true);
    }
  };
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Layout ref={ref} className="pl-4 pr-2 py-3 flex-col white-glow-shadow">
        <Text
          className="pr-3"
          color={err ? "lightRed" : "lightGold"}
          align="center"
        >
          {err
            ? "You cannot combine these Setups."
            : "Choose at least 2 setups with the same party members."}
        </Text>
        <Menu
          options={setups.filter((st) => {
            return !(
              st.type !== "original" || st.party.some((tm) => tm.name === null)
            );
          })}
          notFull={pickedIDs.length < 4}
          pickedIDs={pickedIDs}
          setPickedIDs={setPickedIDs}
        />
        <div className="pr-3 flex-col">
          <TextInput
            type="text"
            className="mt-4 px-3 py-2"
            borderRadius="0.25rem"
            fontSize="1.25rem"
            align="center"
            value={inp}
            onChange={(e) => {
              const { value } = e.target;
              if (value.length <= 32) setInp(value);
            }}
            onKeyDown={(e) => {
              if (e.key === "Enter" && pickedIDs.length > 1) tryCombine();
            }}
          />
          <ButtonBar
            className="mt-3"
            texts={["Cancel", "Combine"]}
            handlers={[
              close,
              () => {
                if (pickedIDs.length > 1) tryCombine();
              }
            ]}
          />
        </div>
      </Layout>
    </ModalCarpet>
  );
}

export function AddCtrl(props) {
  const { setups, addingID, close } = props;
  const [pickedIDs, setPickedIDs] = useState([]);
  const [err, setErr] = useState(false);

  const dispatch = useDispatch();
  useEffect(() => setErr(false), [pickedIDs]);

  const { name, shownID, allIDs } = setups.find((st) => st.ID === addingID);
  const { char, party } = setups.find((st) => st.ID === shownID);
  const all = [
    char.name,
    ...party.filter((tm) => tm.name).map((tm) => tm.name)
  ];
  const remains = all.filter((n) => !allIDs[n]);
  const options = setups.filter((st) => {
    return !(
      st.type !== "original" ||
      st.party.some((tm) => tm.name === null || !all.includes(tm.name)) ||
      !remains.includes(st.char.name)
    );
  });
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Layout ref={ref} className="pl-4 pr-2 py-3 flex-col white-glow-shadow">
        <Text
          className="pr-3"
          color={err ? "lightRed" : "lightGold"}
          align="center"
        >
          {err ? (
            "These 2 Setups feature the same Character."
          ) : (
            <>
              Choose setups to be combined into "<b>{name}</b>".
            </>
          )}
        </Text>
        <Menu
          options={options}
          notFull={pickedIDs.length < remains.length}
          pickedIDs={pickedIDs}
          setPickedIDs={setPickedIDs}
        />
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Combine"]}
          handlers={[
            close,
            () => {
              if (pickedIDs.length) {
                const charNames = [];
                const indexes = [];
                for (const pID of pickedIDs) {
                  const index = setups.findIndex((st) => st.ID === pID);
                  charNames.push(setups[index].char.name);
                  indexes.push(index);
                }
                if (charNames[0] === charNames[1]) {
                  setErr(true);
                } else {
                  dispatch(
                    ADD_TO_COMPLEX({
                      charNames,
                      indexes,
                      complexID: addingID,
                      pickedIDs
                    })
                  );
                  close();
                }
              }
            }
          ]}
        />
      </Layout>
    </ModalCarpet>
  );
}
