import { useState } from "react";
import { artifactIcons, artTypes, wikia } from "../../../data/misc";
import { useCloseModal } from "../../../helpers/hooks";
import { findArtifact, findWeapon } from "../../../helpers/misc";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box, ModalCarpet } from "../../../styledCpns/Layout";
import ArtifactCard from "../../Readers/ArtifactCard";
import WeaponCard from "../../Readers/WeaponCard";

const GearIcon = ({ bgImg, popModal, beta, icon }) => (
  <div className="p-1 glow-on-hover">
    <Box
      className={"p-1" + (bgImg ? " pointer" : "")}
      bgImg={bgImg}
      borderRadius="0.25rem"
      onClick={popModal}
    >
      <Img width="4.25rem" src={beta ? icon : wikia + icon + ".png"} alt="" />
    </Box>
  </div>
);

export default function Gears(props) {
  const { weapon, artPieces } = props;
  const { beta, icon, rarity } = findWeapon(weapon);
  const [type, setType] = useState(null);
  return (
    <Box className="flex-wrap" width="15.75rem">
      <GearIcon
        bgImg={rarity}
        beta={beta}
        icon={icon}
        popModal={() => setType("weapon")}
      />
      {artPieces.map((artP, i) => {
        if (artP) {
          const artData = findArtifact(artP);
          return (
            <GearIcon
              key={i}
              bgImg={artP.rarity || 5}
              beta={artData.beta}
              icon={artData.icon}
              popModal={() => setType("artifacts")}
            />
          );
        }
        return <GearIcon key={i} icon={artifactIcons[artTypes[i]]} />;
      })}
      {type ? (
        <GearReader
          type={type}
          weapon={weapon}
          artPieces={artPieces}
          close={() => setType(null)}
        />
      ) : null}
    </Box>
  );
}

function GearReader(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-3 py-4 flex overflow-auto white-glow-shadow max-width-95"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        {props.type === "weapon" ? (
          <Box className="hide-sb" width="18.75rem" height="30rem">
            <WeaponCard mutable={false} weapon={props.weapon} />
          </Box>
        ) : (
          props.artPieces.map((artP, i) =>
            artP ? (
              <Box key={i} className="px-1" width="14.5rem">
                <ArtifactCard mutable={false} art={artP} ssSpace={2} />
              </Box>
            ) : null
          )
        )}
      </Box>
    </ModalCarpet>
  );
}
