import { memo, useState } from "react";
import isEqual from "react-fast-compare";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../../data/characters";
import { restoreAllMCs } from "../../../helpers/processors";
import { PREPARE_IMPORT } from "../../../redux/calculatorSlice";
import { CHOOSE_SETUP, UNCOMBINE_COMPLEX } from "../../../redux/setupsSlice";
import { Card } from "../../../styledCpns/Database";
import { Text } from "../../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import Gears from "./Gears";
import Members from "./Members";
import { Divider, LinkIcon } from "./styles";

function reCreateSetup(setup) {
  const {
    ID,
    name,
    type,
    char,
    customMCs = { BCs: [], DCs: [] },
    target
  } = setup;
  return { ID, name, type, char, ...restoreAllMCs(setup), customMCs, target };
}

export default memo(Setup, isEqual);

function Setup({
  ID,
  setup,
  stName,
  allIDs,
  setSharedID,
  setRemovedID,
  setAddingID,
  buildTmSt,
  setModalType
}) {
  const { chosenID } = useSelector((state) => state.setups);
  const { char } = setup;
  const isOg = setup.type === "original";
  const charData = characters[char.name];
  const dispatch = useDispatch();
  return (
    <Box
      className="mb-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={ID === chosenID ? "0 0 5px 3px green" : "var(--cmn-shadow)"}
      onClick={() => dispatch(CHOOSE_SETUP(ID))}
    >
      <div className="px-2 flex justify-between">
        <Box className="flex align-center" width="24rem">
          {!isOg && <LinkButton ID={ID} shownID={setup.ID} />}
          <Text className="ellipsis" variant="h3" color="orange">
            {stName || setup.name}
          </Text>
        </Box>
        <div className="flex pb-3">
          <IconBtn
            icon="fas fa-wrench"
            className="mr-4 p-2 glow-on-hover"
            variant="positive"
            onClick={() => dispatch(PREPARE_IMPORT(reCreateSetup(setup)))}
          />
          <IconBtn
            icon="fas fa-share-alt"
            className="mr-4 p-2 glow-on-hover"
            variant="neutral"
            onClick={() => setSharedID(setup.ID)}
          />
          {isOg ? (
            <IconBtn
              icon="fas fa-trash-alt"
              className="glow-on-hover"
              variant="negative"
              onClick={() => setRemovedID(ID)}
            />
          ) : (
            <AddButton
              addable={Object.keys(allIDs).length < 4}
              add={() => setAddingID(ID)}
            />
          )}
        </div>
      </div>
      <div className="flex">
        <Card className="flex">
          <Members
            ID={ID}
            char={char}
            charData={charData}
            isOg={isOg}
            party={setup.party}
            buildTmSt={(tmI) => buildTmSt(setup, tmI)}
            allIDs={allIDs}
          />
          <Divider />
          <div>
            <Box className="mb-2 flex justify-center">
              {["Stats", "Modifiers"].map((type, i) => (
                <Button
                  key={i}
                  className="mx-2 glow-on-hover"
                  variant="default"
                  onClick={() => setModalType(type)}
                >
                  {type}
                </Button>
              ))}
            </Box>
            <Gears weapon={setup.weapon} artPieces={setup.art.pieces} />
          </div>
        </Card>
      </div>
    </Box>
  );
}

function AddButton({ addable, add }) {
  return (
    <IconBtn
      icon="fas fa-plus"
      className={addable ? "glow-on-hover" : "unavailable"}
      variant="neutral"
      onClick={() => {
        if (addable) add();
      }}
    />
  );
}

function LinkButton({ ID, shownID }) {
  const dispatch = useDispatch();
  const uncombine = () => {
    dispatch(UNCOMBINE_COMPLEX(ID));
    setTimeout(() => {
      dispatch(CHOOSE_SETUP(shownID));
    }, 100);
  };
  if (window.innerWidth <= 1050) {
    return (
      <IconBtn
        icon="fas fa-unlink"
        className="mr-2 glow-on-hover"
        variant="negative"
        onClick={uncombine}
      />
    );
  }
  return <PCVer uncombine={uncombine} />;
}

function PCVer({ uncombine }) {
  const [hovered, setHovered] = useState(false);
  return (
    <LinkIcon
      className={"mr-2 flex-center pointer " + (hovered ? "unlink" : "link")}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={uncombine}
    >
      <i className={"fas fa-" + (hovered ? "unlink" : "link")} />
    </LinkIcon>
  );
}
