import { createSlice } from "@reduxjs/toolkit";

export const calculatorSlice = createSlice({
  name: "calculator",
  initialState: {
    importedSetup: null
  },
  reducers: {
    PREPARE_IMPORT: (state, action) => {
      state.importedSetup = action.payload;
    }
  }
});

export const { PREPARE_IMPORT } = calculatorSlice.actions;

export default calculatorSlice.reducer;
