import { useCallback, useContext, useState } from "react";
import { colorCodes } from "../../../../data/misc";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Button } from "../../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../../styledCpns/Layout";
import { ButtonBar, CloseBtn } from "../../../CommonCpns";
import { HiddenScroll } from "../../../CommonCpns/HiddenSpace";
import { getNewSetupName, getSetupCtrl } from "../../helpers";
import { CharCt, FixedCts, ManagerCts, SettingsCt, SetupCt } from "../../index";
import Configs from "./Configs";
import { SaveCtrl, SharedCtrl } from "./ExtraCtrls";
import { applySettings } from "./helpers";
import SetupCtrl from "./SetupCtrl";

export default function HiddenSettings({ height, ...others }) {
  const { on } = useContext(SettingsCt);
  return (
    <AbsoluteBox className="full-w" bottom={0} left={0}>
      <HiddenScroll
        on={on}
        style={{
          borderRadius: "0.625rem",
          backgroundColor: colorCodes.cardBg3
        }}
        openHeight={height / 16 + 2 + "rem"}
        duration={250}
        timingFunc="ease-in-out"
      >
        <Settings {...others} />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function Settings({ importBtn }) {
  const char = useContext(CharCt);
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const curSetupI = useContext(SetupCt);
  const {
    setChar,
    setSelfMCs,
    setWpRack,
    setSubWpMCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setCustomMCs,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs,
    setSettings
  } = useContext(FixedCts);
  const { on, ...configs } = useContext(SettingsCt);
  const [tempSetups, setTempSetups] = useState(
    setups.map((st, index) => {
      const temp = {
        ...st,
        index,
        checked: cmpSetupIs.includes(index),
        expanded: false
      };
      if (index === stdSetupI) temp.isStandard = true;
      if (index === curSetupI) temp.isCurSetupI = true;
      return temp;
    })
  );
  const [tempConfigs, setTempConfigs] = useState({ ...configs });
  const [err, setErr] = useState(false);
  const [savedI, setSavedI] = useState(null);
  const [sharedI, setSharedI] = useState(null);

  const close = () => setSettings((prev) => ({ ...prev, on: false }));

  const tryApply = useCallback(() => {
    let ok = true;
    const names = [];
    for (const { name } of tempSetups) {
      if (!name.length || names.includes(name)) {
        ok = false;
        break;
      } else {
        names.push(name);
      }
    }
    if (ok) {
      applySettings(tempSetups, configs, tempConfigs, char, {
        setSetups,
        setCmpSetupIs,
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setCustomMCs,
        setStdSetupI,
        setCurSetupI,
        setSettings,
        setChar
      });
    } else {
      setErr(true);
    }
  }, [tempSetups, char, tempConfigs]);

  return (
    <div className="p-3 full-h flex-col">
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <CloseBtn className="glow-on-hover" onClick={close} />
      </AbsoluteBox>
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box className="mt-2 grow-1 flex-col hide-sb">
        {tempSetups.map((setup, index) => (
          <SetupCtrl
            key={index}
            setup={setup}
            index={index}
            belowLimit={tempSetups.length < 4}
            setTempSetups={setTempSetups}
            save={() => setSavedI(index)}
            share={() => setSharedI(index)}
          />
        ))}
        {tempSetups.length < 4 && (
          <div className="mt-3">
            <Box className="mb-4" height="2rem">
              <Button
                className="full-h full-w glow-on-hover"
                bgColor="#2269d6"
                color="white"
                borderRadius="1rem"
                onClick={() => {
                  setTempSetups((prev) => {
                    const newTSs = [...prev];
                    newTSs.push({
                      ...getSetupCtrl({ name: getNewSetupName(prev) }),
                      index: null,
                      checked: false,
                      expanded: false
                    });
                    return newTSs;
                  });
                }}
              >
                <i className="fas fa-plus" />
              </Button>
            </Box>
            {importBtn}
          </div>
        )}
        <Configs configs={tempConfigs} setConfigs={setTempConfigs} />
        {err && (
          <Text className="mt-3" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <ButtonBar
        className="mt-3"
        variants={["positive"]}
        texts={["Apply"]}
        handlers={[tryApply]}
      />
      {savedI !== null && (
        <SaveCtrl
          savedSetup={tempSetups[savedI]}
          closeSettings={close}
          close={() => setSavedI(null)}
        />
      )}
      {sharedI !== null && (
        <SharedCtrl
          sharedSetup={tempSetups[sharedI]}
          close={() => setSharedI(null)}
        />
      )}
    </div>
  );
}
