import { useCallback, useContext, useState } from "react";
import { useSelector } from "react-redux";
import characters from "../../../data/characters";
import { levels, wikia } from "../../../data/misc";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { AbsoluteBox } from "../../../styledCpns/Layout";
import { BetaMark, ButtonBar, StarLine } from "../../CommonCpns";
import Picker from "../../Picker";
import {
  getSetupCtrl,
  initCharMCs,
  initElmtMCs,
  initMonster,
  initParty,
  initSubWpMCs,
  initSettings,
  initCustomMCs
} from "../helpers";
import { CharCt, SetupCt, FixedCts } from "../index";
import { Card, MainSelect } from "../styles";
import { getMixedCharList, parseData } from "./helpers";
import TabContent from "./TabContent";

export default function CalcChar() {
  const char = useContext(CharCt);
  const { setChar } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const [tab, setTab] = useState("Attributes");
  const [pickerOn, setPickerOn] = useState(false);

  const charPicker = pickerOn && (
    <CharPicker close={() => setPickerOn(false)} />
  );
  if (char === null) {
    return (
      <Card bgColor="cardBg1">
        <ButtonBar
          className="mt-3"
          texts={["Choose a Character"]}
          variants={["positive"]}
          handlers={[() => setPickerOn(true)]}
        />
        {charPicker}
      </Card>
    );
  }
  const sepCharInfo = Array.isArray(char.level);
  const charData = characters[char.name];
  return (
    <Card className="px-4 py-3 flex-col" bgColor="cardBg1">
      <div className="mt-2 pb-4 flex">
        <div className="mr-3 relative" onClick={() => setPickerOn(true)}>
          <AbsoluteBox top="-0.625rem" left="-0.625rem" zIndex={1}>
            <IconBtn
              className="glow-on-hover"
              icon="fas fa-sync-alt"
              variant="positive"
              fontSize="1.25rem"
            />
          </AbsoluteBox>
          {charData.beta && (
            <AbsoluteBox top="-0.5rem" right="-0.5rem" zIndex={1}>
              <BetaMark />
            </AbsoluteBox>
          )}
          <Img
            className="pointer"
            width="6.25rem"
            src={wikia + charData.icon + ".png"}
            alt={char.name}
            draggable={false}
          />
        </div>
        <div className="overflow-hidden">
          <Text className="ellipsis" variant="h1" color={charData.vision}>
            {char.name}
          </Text>
          <StarLine className="mt-1" rarity={charData.rarity} />
          <div className="mt-1 flex">
            <Text variant="h6" className="mr-1">
              Level
            </Text>
            <Select
              color={charData.vision}
              fontSize="1.125rem"
              fontWeight={700}
              align="right"
              value={sepCharInfo ? char.level[curSetupI] : char.level}
              onChange={(e) =>
                setChar((prev) => {
                  const newChar = { ...prev };
                  const level = e.target.value;
                  const maxLv = +level.slice(-2);
                  const ascension = maxLv === 20 ? 0 : (maxLv - 30) / 10;
                  if (sepCharInfo) {
                    newChar.level[curSetupI] = level;
                    newChar.ascension[curSetupI] = ascension;
                  } else {
                    newChar.level = level;
                    newChar.ascension = ascension;
                  }
                  return newChar;
                })
              }
            >
              {levels.map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          </div>
        </div>
      </div>
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={[
          "Attributes",
          "Weapon",
          "Artifacts",
          "Constellation",
          "Talents"
        ]}
      />
      <TabContent tab={tab} />
      {charPicker}
    </Card>
  );
}

function CharPicker(props) {
  const { myChars, myWps, myArts } = useSelector((state) => state.database);
  const {
    setChar,
    setSelfMCs,
    setWpRack,
    setSubWpMCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setCustomMCs,
    setMonster,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs,
    setSettings
  } = useContext(FixedCts);

  const pickChar = useCallback((unit) => {
    const { char, wp, art } = parseData(unit, myWps, myArts);
    setChar(char);
    setSelfMCs([initCharMCs(char.name, true)]);
    setWpRack([wp]);
    setSubWpMCs([initSubWpMCs()]);
    setArtChest([art]);
    setParties([initParty()]);
    setElmtMCs([initElmtMCs()]);
    setCustomMCs([initCustomMCs()]);
    setMonster(initMonster());
    setSetups([getSetupCtrl({})]);
    setCurSetupI(0);
    setStdSetupI(0);
    setCmpSetupIs([0]);
    setSettings(initSettings());
  }, []);
  return (
    <Picker
      data={getMixedCharList(myChars)}
      dataType="Character"
      close={props.close}
      pick={pickChar}
    />
  );
}
