import { levels, pctSTypes } from "../data/misc";
import { tfmRxns } from "../data/elements";
import characters from "../data/characters";
import weapons from "../data/weapons";
import artifacts from "../data/artifacts";

export function mapToLevels(arr) {
  const result = {};
  levels.forEach((lv, i) => {
    result[lv] = arr[i];
  });
  return result;
}

export const pctOf = (type) => (pctSTypes.includes(type) ? "%" : "");

export function deepCopy(item) {
  return JSON.parse(JSON.stringify(item));
}

export function getNamesForTables(charName) {
  const result = [];
  for (const talent of characters[charName].actvTalents) {
    if (talent.type !== "Alternate Sprint") {
      result.push({
        main: talent.type,
        subs: talent.stats
          .filter(({ noCalc }) => !noCalc)
          .map(({ name }) => name)
      });
    }
  }
  result.push({
    main: "Reactions DMG",
    subs: Object.keys(tfmRxns)
  });
  return result;
}

export const initArtStatFilter = () => ({
  main: "All",
  subs: [1, 2, 3, 4].map(() => "All")
});

export function findWeapon({ type, code }) {
  return weapons[type].find((wp) => wp.code === code);
}

export function findArtifact({ code, type }) {
  const tgSet = artifacts.find((set) => set.code === code);
  if (type) {
    const { name, icon } = tgSet[type];
    return { beta: tgSet.beta, name, icon };
  }
  return tgSet;
}

export function getAcronym(word) {
  return {
    "Normal Attack": "NA",
    "Charged Attack": "CA",
    "Plunging Attack": "PA",
    "Elemental Skill": "ES",
    "Elemental Burst": "EB"
  }[word];
}
