import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import characters from "../../data/characters";
import { colorCodes } from "../../data/misc";
import { useCloseModal } from "../../helpers/hooks";
import { deepCopy } from "../../helpers/misc";
import { SORT_CHARS } from "../../redux/databaseSlice";
import { ColoredSpan, Text } from "../../styledCpns/DataDisplay";
import { AbsoluteBox, ModalCarpet } from "../../styledCpns/Layout";
import { ButtonBar, CloseBtn } from "../CommonCpns";

export default function CharSorting({ close }) {
  const processedChars = useSelector((state) => state.database.myChars).map(
    ({ name, level }, index) => {
      return { name, level, rarity: characters[name].rarity, index };
    }
  );
  const [list, setList] = useState(processedChars);
  const [dragIndex, setDragIndex] = useState(null);
  const [dropIndex, setDropIndex] = useState(null);
  const dispatch = useDispatch();

  const handleDragStart = (e) => {
    setDragIndex(+e.target.id);
  };
  const handleDragEnter = (e) => {
    setDropIndex(+e.target.id);
  };
  const handleDragOver = (e) => {
    e.preventDefault();
  };
  const handleDrop = () => {
    if (dropIndex !== dragIndex) {
      setList((prev) => {
        const newList = deepCopy(prev);
        newList.splice(
          dragIndex < dropIndex ? dropIndex - 1 : dropIndex,
          0,
          newList.splice(dragIndex, 1)[0]
        );
        return newList;
      });
    }
    setDropIndex(null);
  };

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <StyledDiv ref={ref} className="px-2 py-3 relative white-glow-shadow">
        <AbsoluteBox top="0.5rem" right="0.5rem">
          <CloseBtn className="glow-on-hover" onClick={close} />
        </AbsoluteBox>
        <Text variant="h4" color="orange" align="center">
          Sort by
        </Text>
        <ButtonBar
          className="mt-3"
          distance="ml-3"
          texts={["Name", "Level", "Rarity"]}
          variants={["neutral", "neutral", "neutral"]}
          handlers={[
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                newList.sort((a, b) => a.name.localeCompare(b.name));
                return newList;
              }),
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                return newList.sort((a, b) => {
                  const [fA, sA] = a.level.split("/").map((lv) => +lv);
                  const [fB, sB] = b.level.split("/").map((lv) => +lv);
                  if (fA !== fB) {
                    return fB - fA;
                  }
                  return sB - sA;
                });
              }),
            () =>
              setList((prev) => {
                const newList = deepCopy(prev);
                return newList.sort((a, b) => {
                  return b.rarity - a.rarity;
                });
              })
          ]}
        />
        <div className="list_wrapper mt-3 custom-sb">
          <div>
            {list.map((unit, i) => (
              <div
                key={i}
                id={i}
                className={
                  "line px-2 py-1 flex align-center" +
                  (dropIndex === i ? " dragged-over" : "")
                }
                draggable="true"
                onDragStart={handleDragStart}
                onDragOver={handleDragOver}
                onDragEnter={handleDragEnter}
                onDrop={handleDrop}
              >
                <button className="grab_btn mr-2 no-pointer-events">
                  <i className="fas fa-sort" />
                </button>
                <Text className="no-pointer-events">
                  <ColoredSpan color={unit.rarity}>
                    <strong>{unit.name}</strong>
                  </ColoredSpan>{" "}
                  (Lv. {unit.level})
                </Text>
              </div>
            ))}
          </div>
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Confirm"]}
          variants={["positive"]}
          handlers={[
            () => {
              dispatch(SORT_CHARS(list.map(({ index }) => index)));
              close();
            }
          ]}
        />
      </StyledDiv>
    </ModalCarpet>
  );
}

const StyledDiv = styled.div`
  width: 90%;
  max-width: 22rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg2};
  .list_wrapper {
    height: 60vh;
  }
  .line {
    cursor: default;
    user-select: none;
  }
  .line:hover {
    background-color: ${colorCodes.cardBg1};
  }
  .grab_btn {
    width: 2rem;
    height: 2rem;
    border: none;
    color: ${colorCodes.defaultBg};
    background-color: transparent;
    font-size: 1.25rem;
  }
  .no-pointer-events {
    pointer-events: none;
  }
  .dragged-over {
    border-top: 1px solid white;
  }
`;
