import artifactDebuffs from "../../../data/artifacts/artifactDebuffs";
import characters from "../../../data/characters";
import { elmtDebuffDesc, rsnNames } from "../../../data/elements";
import { findArtifact } from "../../../helpers/misc";
import { Text } from "../../../styledCpns/DataDisplay";
import { NoContent } from "../../Calculator/Modifiers/CmnCpns";
import { ExpandableList } from "../../CommonCpns";
import { ModReader } from "./styles";

export default function Debuffs(props) {
  const headingList = [
    "Resonance & Reactions",
    "Self",
    "Party",
    "Artifacts",
    "Custom"
  ];
  return (
    <ExpandableList
      headingList={headingList}
      contentList={headingList.map((cate, i) => (
        <Inner key={i} cate={cate} {...props} />
      ))}
    />
  );
}

function Inner(props) {
  const { cate, elmtMCs, char, party } = props;
  let content = [];
  if (cate === "Resonance & Reactions") {
    if (elmtMCs.superconduct) {
      content.push(
        <ModReader
          key="sc"
          heading="Superconduct"
          desc={elmtDebuffDesc.Superconduct}
        />
      );
    }
    const geoName = rsnNames.Geo;
    if (elmtMCs.resonance.some((rsn) => rsn.name === geoName)) {
      content.push(
        <ModReader key="geo" heading={geoName} desc={elmtDebuffDesc[geoName]} />
      );
    }
  } else if (cate === "Self") {
    content = props.selfDCs.map(({ index }, i) => {
      const debuff = characters[char.name].debuffs[index];
      return (
        <ModReader
          key={i}
          heading={debuff.src}
          desc={debuff.desc({
            fromSelf: true,
            char,
            party
          })}
        />
      );
    });
  } else if (cate === "Party") {
    for (const tm of party) {
      if (tm) {
        const { name, DCs } = tm;
        const tmData = characters[name];
        if (name && DCs.length) {
          content.push(
            <Text
              key={name}
              className="pt-2 mb-m1"
              variant="h6"
              fontWeight={700}
              color={tmData.vision}
              align="center"
            >
              {name.toUpperCase()}
            </Text>
          );
        }
        DCs.forEach(({ index, inputs }, i) => {
          const debuff = tmData.debuffs[index];
          content.push(
            <ModReader
              key={name + i}
              heading={debuff.src}
              desc={debuff.desc({
                fromSelf: false,
                char: props.char,
                inputs
              })}
              labels={debuff.labels}
              inputTypes={debuff.inputTypes}
              inputs={inputs}
            />
          );
        });
      }
    }
  } else if (cate === "Artifacts") {
    content = props.artSubDCs.map(({ code, index, inputs }, i) => {
      const debuff = artifactDebuffs[code][index];
      return (
        <ModReader
          key={i}
          heading={findArtifact({ code }).name}
          desc={debuff.desc}
          labels={debuff.labels}
          inputTypes={debuff.inputTypes}
          inputs={inputs}
        />
      );
    });
  } else if (cate === "Custom") {
    content = props.customDCs.map(({ type, val }, i) => {
      return (
        <div key={i} className="pt-2 flex justify-end">
          <Text className="mr-3">{type}</Text>
          <Text color="orange" fontWeight={700}>
            {val}%
          </Text>
        </div>
      );
    });
  }
  return content.length ? content : <NoContent type="debuffs" />;
}
