import characters from "../../../../data/characters";
import { charInfoFields } from "../../../../data/misc";
import { initWeapon } from "../../../Picker/ItemPicker";
import {
  getMainWpBCs,
  getSubArtBCs,
  getSubArtDCs,
  initCharMCs,
  initCustomMCs,
  initElmtMCs,
  initParty,
  initSubWpMCs
} from "../../helpers";

export function applySettings(tempSetups, configs, tempConfigs, char, CBs) {
  const setups = [];
  const cmpSetupIs = [];
  const indexes = [];
  let standardI;
  let currentI = -1;
  for (const i in tempSetups) {
    const st = tempSetups[i];
    const { name, ID, type } = tempSetups[i];
    setups.push({ name, ID, type });
    indexes.push(st.index);
    if (st.checked) cmpSetupIs.push(+i);
    if (st.isStandard) standardI = +i;
    if (st.isCurrent) currentI = +i;
  }
  function getResult(prev, newElmt) {
    const result = [];
    for (const index of indexes) {
      result.push(index === null ? newElmt : prev[index]);
    }
    return result;
  }
  CBs.setSetups(setups);
  CBs.setCmpSetupIs(cmpSetupIs);
  CBs.setSelfMCs((prev) => getResult(prev, initCharMCs(char.name, true)));
  CBs.setWpRack((prev) => {
    const result = [];
    for (let index of indexes) {
      if (index === null) {
        const { weapon } = characters[char.name];
        const newWp = initWeapon({ type: weapon });
        result.push({ ...newWp, BCs: getMainWpBCs(newWp) });
      } else {
        result.push(prev[index]);
      }
    }
    return result;
  });
  CBs.setSubWpMCs((prev) => getResult(prev, initSubWpMCs()));
  CBs.setArtChest((prev) =>
    getResult(prev, {
      pieces: [null, null, null, null, null],
      sets: [],
      BCs: [],
      subBCs: getSubArtBCs(),
      subDCs: getSubArtDCs()
    })
  );
  CBs.setParties((prev) => getResult(prev, initParty()));
  CBs.setElmtMCs((prev) => getResult(prev, initElmtMCs()));
  CBs.setCustomMCs((prev) => getResult(prev, initCustomMCs()));
  CBs.setStdSetupI(standardI);
  CBs.setCurSetupI(currentI > -1 ? currentI : standardI);
  CBs.setSettings({ ...tempConfigs, on: false });

  if (configs.sepCharInfo || tempConfigs.sepCharInfo) {
    CBs.setChar((prev) => {
      const char = { ...prev };
      for (const field of charInfoFields) {
        if (tempConfigs.sepCharInfo) {
          if (configs.sepCharInfo) {
            char[field] = getResult(char[field], char[field][standardI]);
          } else {
            char[field] = Array(tempSetups.length).fill(char[field]);
          }
        } else if (configs.sepCharInfo) {
          char[field] = char[field][standardI];
        }
      }
      return char;
    });
  }
}
