import { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../data/characters";
import { ADD_CHAR } from "../../redux/databaseSlice";
import { Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import Info from "./Info";
import TopBar from "./TopBar";
import TopBarSmall from "./TopBarSmall";

export default function MyCharacters() {
  const [charIndex, setCharIndex] = useState(0);
  const [pickerOn, setPickerOn] = useState(false);
  const { myChars } = useSelector((state) => state.database);
  const charListRef = useRef();

  return (
    <Box className="full-h flex-col" bgColor="cardBg3">
      {window.innerWidth <= 700 ? (
        <TopBarSmall
          charIndex={charIndex}
          setCharIndex={setCharIndex}
          setPickerOn={setPickerOn}
        />
      ) : (
        <TopBar
          charIndex={charIndex}
          setCharIndex={setCharIndex}
          setPickerOn={setPickerOn}
          charListRef={charListRef}
        />
      )}
      <div className="grow-1 flex-center">
        <Box className="full-w flex justify-center" height="98%">
          {Boolean(myChars.length) && (
            <Info charIndex={charIndex} setCharIndex={setCharIndex} />
          )}
        </Box>
      </div>
      {pickerOn && (
        <CharPicker
          setCharIndex={setCharIndex}
          charListRef={charListRef}
          close={() => setPickerOn(false)}
        />
      )}
    </Box>
  );
}

function CharPicker({ setCharIndex, charListRef, close }) {
  const { myChars } = useSelector((state) => state.database);
  const wishlist = getRemainingChars(myChars);
  const dispatch = useDispatch();
  return (
    <Picker
      needMassAdd={true}
      data={wishlist}
      dataType="Character"
      close={close}
      pick={({ name, weapon }) => {
        dispatch(ADD_CHAR({ name, weapon }));
        setCharIndex(0);
        if (charListRef.current) charListRef.current.scrollLeft = 0;
      }}
    />
  );
}

function getRemainingChars(myChars) {
  const result = [];
  for (let name in characters) {
    let existed = false;
    for (let unit of myChars) {
      if (name === unit.name) {
        existed = true;
        break;
      }
    }
    const { code, beta, icon, rarity, vision, weapon } = characters[name];
    result.push({ code, beta, name, icon, rarity, vision, weapon, existed });
  }
  return result;
}
