import { findArtifact, findWeapon } from "../helpers/misc";

export function getItem(inv, ID) {
  return inv.find((wp) => wp.ID === ID) || {};
}
export function getChar(chars, name) {
  return chars.find((char) => char.name === name) || {};
}

export function sortWeapons(a, b) {
  const rA = findWeapon(a).rarity;
  const rB = findWeapon(b).rarity;
  if (rA !== rB) return rB - rA;
  const [fA, sA] = a.level.split("/").map((lv) => +lv);
  const [fB, sB] = b.level.split("/").map((lv) => +lv);
  if (fA !== fB) {
    return fB - fA;
  }
  if (a.type !== b.type) {
    const type = {
      Bow: 5,
      Catalyst: 4,
      Polearm: 3,
      Claymore: 2,
      Sword: 1
    };
    return type[b.type] - type[a.type];
  }
  return sB - sA;
}

export function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  const aName = findArtifact({ code: a.code }).name;
  const bName = findArtifact({ code: b.code }).name;
  return bName.localeCompare(aName);
}
