import { useState, useEffect, useCallback, createContext } from "react";
import { Box } from "../../styledCpns/Layout";
import { StyledCalculator, Card } from "./styles";
import CalcChar from "./CalcChar";
import Modifiers from "./Modifiers";
import SetupManager from "./SetupManager";
import DmgResults from "./DmgResults";
import ImportCtrl from "./ImportCtrl";
import characters from "../../data/characters";
import monsters from "../../data/monsters";
import { elmts } from "../../data/elements";
import { initTarget, initMonster, initSession, addSetup } from "./helpers";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import { deepCopy } from "../../helpers/misc";
import { getFinalInfusion, getThisChar } from "../../helpers/processors";
import { useDispatch, useSelector } from "react-redux";
import { CHANGE_TAB } from "../../redux/uiSlice";
import { PREPARE_IMPORT } from "../../redux/calculatorSlice";

export const FixedCts = createContext();
export const SettingsCt = createContext();
export const CharCt = createContext();
export const SelfMCsCt = createContext();
export const WpRackCt = createContext();
export const SubWpMCsCt = createContext();
export const ArtChestCt = createContext();
export const PartiesCt = createContext();
export const ElmtMCsCt = createContext();
export const CustomMCsCt = createContext();
export const TargetCt = createContext();
export const ResultCt = createContext();
export const SetupCt = createContext();
export const ManagerCts = createContext();

export default function Calculators() {
  const { importedSetup } = useSelector((state) => state.calculator);
  const [char, setChar] = useState(null);
  const [selfMCs, setSelfMCs] = useState([]);
  const [wpRack, setWpRack] = useState([]);
  const [subWpMCs, setSubWpMCs] = useState([]);
  const [artChest, setArtChest] = useState([]);
  const [parties, setParties] = useState([]);
  const [elmtMCs, setElmtMCs] = useState([]);
  const [customMCs, setCustomMCs] = useState([]);
  const [target, setTarget] = useState(initTarget());
  const [setups, setSetups] = useState([]);
  const [curSetupI, setCurSetupI] = useState(0);
  const [stdSetupI, setStdSetupI] = useState(0);
  const [cmpSetupIs, setCmpSetupIs] = useState([0]);
  const [settings, setSettings] = useState();

  const dispatch = useDispatch();

  const [monster, setMonster] = useState(initMonster());
  useEffect(() => {
    setTarget((prev) => {
      const newTg = deepCopy(prev);
      const { res, addResBn } = monsters[
        monsters.findIndex(({ name }) => name === monster.name)
      ];
      for (let type in res) {
        if (type === "Elemental RES") {
          for (let elmt of elmts) {
            newTg[elmt + " RES"] = res[type];
          }
        } else {
          newTg[type] = res[type];
        }
      }
      if (addResBn) {
        addResBn({ target: newTg, inputs: monster.inputs });
      }
      return newTg;
    });
  }, [monster]);

  const [importType, setImportType] = useState(null);

  const initNewSession = useCallback(() => {
    if (importedSetup) {
      initSession(importedSetup, {
        setChar,
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setCustomMCs,
        setSetups,
        setTarget,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs,
        setSettings
      });
      dispatch(CHANGE_TAB("Calculator"));
      dispatch(PREPARE_IMPORT(null));
    }
  }, [importedSetup]);

  const addNewSetup = useCallback(
    (overrideChar, overrideTg) => {
      if (importedSetup) {
        addSetup(importedSetup, overrideChar, overrideTg, setups.length, {
          setChar,
          setTarget,
          setSelfMCs,
          setWpRack,
          setSubWpMCs,
          setArtChest,
          setParties,
          setElmtMCs,
          setCustomMCs,
          setSetups,
          setCurSetupI,
          setSettings
        });
        dispatch(CHANGE_TAB("Calculator"));
        dispatch(PREPARE_IMPORT(null));
      }
    },
    [importedSetup, setups.length]
  );

  useEffect(() => {
    if (!importedSetup) return;
    if (char) {
      if (char.name === importedSetup.char.name) {
        if (setups.length === 4) {
          setImportType(2);
        } else {
          let sameInfo = true;
          for (let k in char) {
            if (char[k] !== importedSetup.char[k]) {
              sameInfo = false;
              break;
            }
          }
          let sameTg = true;
          for (let type in target) {
            if (target[type] !== importedSetup.target[type]) {
              sameTg = false;
              break;
            }
          }
          if (sameInfo && sameTg) {
            addNewSetup(false, false);
          } else {
            let code = 3;
            if (!sameInfo) code = code * 10;
            if (!sameTg) code = code * 10 + 1;
            setImportType(code);
          }
        }
      } else {
        setImportType(1);
      }
    } else {
      initNewSession();
      // addNewSetup(true, true);
    }
  }, [importedSetup]);

  let charData;
  const allATTRs = [];
  const allArtSBnes = [];
  const allRxnBnes = [];
  const allInfusion = [];
  const allDmg = [];
  if (char) {
    charData = characters[char.name];
    for (let i = 0; i < setups.length; i++) {
      const thisChar = getThisChar(char, i);
      const finalInfusion = getFinalInfusion(
        thisChar,
        selfMCs[i].BCs,
        charData.vision,
        parties[i]
      );
      allInfusion.push(finalInfusion);
      const [ATTRs, bonuses, rxnBnes, artSBnes] = getBuffedStats(
        charData,
        thisChar,
        selfMCs[i].BCs,
        wpRack[i],
        subWpMCs[i].BCs,
        artChest[i],
        elmtMCs[i].resonance,
        parties[i],
        customMCs[i].BCs,
        finalInfusion
      );
      allATTRs.push(ATTRs);
      allArtSBnes.push(artSBnes);
      allRxnBnes.push(rxnBnes);
      allDmg.push(
        getDmg(
          thisChar,
          selfMCs[i],
          parties[i],
          artChest[i].subDCs,
          ATTRs,
          bonuses,
          rxnBnes,
          customMCs[i].DCs,
          finalInfusion,
          elmtMCs[i],
          target
        )
      );
    }
  }
  return (
    <FixedCts.Provider
      value={{
        setChar,
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setCustomMCs,
        setTarget,
        setMonster,
        setSetups,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs,
        setSettings
      }}
    >
      <SettingsCt.Provider value={settings}>
        <CharCt.Provider value={char}>
          <SelfMCsCt.Provider value={selfMCs}>
            <WpRackCt.Provider value={wpRack}>
              <SubWpMCsCt.Provider value={subWpMCs}>
                <ArtChestCt.Provider value={artChest}>
                  <PartiesCt.Provider value={parties}>
                    <ElmtMCsCt.Provider value={elmtMCs}>
                      <CustomMCsCt.Provider value={customMCs}>
                        <TargetCt.Provider value={{ target, monster }}>
                          <ResultCt.Provider
                            value={{
                              allInfusion,
                              allATTRs,
                              allArtSBnes,
                              allRxnBnes,
                              allDmg
                            }}
                          >
                            <SetupCt.Provider value={curSetupI}>
                              <StyledCalculator>
                                <Box className="flex" height="98%">
                                  <CalcChar />
                                  <ManagerCts.Provider
                                    value={{ setups, stdSetupI, cmpSetupIs }}
                                  >
                                    {char ? (
                                      <Modifiers />
                                    ) : (
                                      <Card bgColor="cardBg1" />
                                    )}
                                    <SetupManager />
                                    {char ? (
                                      <DmgResults />
                                    ) : (
                                      <Card bgColor="cardBg3" />
                                    )}
                                  </ManagerCts.Provider>
                                  {importType ? (
                                    <ImportCtrl
                                      type={importType}
                                      importedSetup={importedSetup}
                                      initNewSession={initNewSession}
                                      addNewSetup={addNewSetup}
                                      close={() => {
                                        setImportType(null);
                                        // setImportedSt(null);
                                      }}
                                    />
                                  ) : null}
                                </Box>
                              </StyledCalculator>
                            </SetupCt.Provider>
                          </ResultCt.Provider>
                        </TargetCt.Provider>
                      </CustomMCsCt.Provider>
                    </ElmtMCsCt.Provider>
                  </PartiesCt.Provider>
                </ArtChestCt.Provider>
              </SubWpMCsCt.Provider>
            </WpRackCt.Provider>
          </SelfMCsCt.Provider>
        </CharCt.Provider>
      </SettingsCt.Provider>
    </FixedCts.Provider>
  );
}
