import { createSlice } from "@reduxjs/toolkit";
import { initWeapon } from "../components/Picker/ItemPicker";
import { artTypes } from "../data/misc";
import { processNumInp } from "../helpers/processors";
import { getChar, getItem, sortArtifacts, sortWeapons } from "./helpers";

export const databaseSlice = createSlice({
  name: "database",
  initialState: {
    myChars: [],
    myWps: [],
    myArts: []
  },
  reducers: {
    UPLOAD_DB: (_, action) => {
      return action.payload;
    },
    ADD_CHAR: (state, action) => {
      const { name, weapon } = action.payload;
      const weaponID = Date.now();
      state.myChars.unshift({
        name,
        level: "1/20",
        ascension: 0,
        "Normal Attack": 1,
        "Elemental Skill": 1,
        "Elemental Burst": 1,
        constellation: 0,
        weaponID,
        artIDs: [null, null, null, null, null]
      });
      state.myWps.unshift({
        ID: weaponID,
        user: name,
        ...initWeapon({ type: weapon })
      });
    },
    SORT_CHARS: (state, action) => {
      state.myChars = action.payload.map((index) => state.myChars[index]);
    },
    TOGGLE_CONS: (state, action) => {
      const { name, consIndex } = action.payload;
      const char = getChar(state.myChars, name);
      char.constellation =
        char.constellation === consIndex + 1 ? consIndex : consIndex + 1;
    },
    CHANGE_TALENT: (state, action) => {
      const { name, type, level } = action.payload;
      getChar(state.myChars, name)[type] = level;
    },
    CHANGE_CHAR_LEVEL: (state, action) => {
      const { name, level } = action.payload;
      const maxLv = +level.slice(-2);
      const char = getChar(state.myChars, name);
      char.level = level;
      char.ascension = maxLv === 20 ? 0 : maxLv / 10 - 3;
    },
    REMOVE_CHAR: (state, action) => {
      const { name, setCharIndex } = action.payload;
      const { myChars, myWps, myArts } = state;
      const charIndex = myChars.findIndex((char) => char.name === name);
      const { weaponID, artIDs } = myChars[charIndex];
      getItem(myWps, weaponID).user = null;
      for (const id of artIDs) {
        if (id) getItem(myArts, id).user = null;
      }
      myChars.splice(charIndex, 1);
      if (charIndex > 0 && charIndex === myChars.length) {
        setCharIndex((prev) => prev - 1);
      }
    },
    SWITCH_WP: (state, action) => {
      const { newUser, newID, oldUser, oldID } = action.payload;
      const { myWps, myChars } = state;
      getItem(myWps, newID).user = oldUser;
      getItem(myWps, oldID).user = newUser;
      getChar(myChars, oldUser).weaponID = newID;
      if (newUser) {
        getChar(myChars, newUser).weaponID = oldID;
      }
    },
    SWITCH_ART: (state, action) => {
      const { newUser, newID, oldUser, oldID, pos } = action.payload;
      const { myArts, myChars } = state;
      getItem(myArts, newID).user = oldUser;
      if (oldID) {
        getItem(myArts, oldID).user = newUser;
      }
      getChar(myChars, oldUser).artIDs[pos] = newID;
      if (newUser) {
        getChar(myChars, newUser).artIDs[pos] = oldID || null;
      }
    },
    UNEQUIP_ART: (state, action) => {
      const { user, artID, pos } = action.payload;
      getChar(state.myChars, user).artIDs[pos] = null;
      getItem(state.myArts, artID).user = null;
    },
    ADD_WP: (state, action) => {
      state.myWps.unshift(action.payload);
    },
    SORT_WPS: (state) => {
      state.myWps.sort((a, b) => sortWeapons(a, b));
    },
    SWAP_WP_USER: (state, action) => {
      const { newUser, wpID, oldUser } = action.payload;
      const { myChars, myWps } = state;
      getItem(myWps, wpID).user = newUser;
      const newChar = getChar(myChars, newUser);
      const { weaponID } = newChar;
      if (weaponID) {
        getItem(myWps, weaponID).user = oldUser;
      }
      if (oldUser) {
        getChar(myChars, oldUser).weaponID = weaponID;
      }
      newChar.weaponID = wpID;
    },
    REMOVE_WP: (state, action) => {
      const { ID, user, type, setChosenID } = action.payload;
      const { myWps, myChars } = state;
      let index = myWps.findIndex((wp) => wp.ID === ID);
      myWps.splice(index, 1);
      if (myWps.length > 0) {
        if (index === myWps.length) index--;
        setChosenID(myWps[index].ID);
      }
      if (user) {
        const newWpID = Date.now();
        myWps.unshift({
          ID: newWpID,
          user,
          ...initWeapon({ type })
        });
        getChar(myChars, user).weaponID = newWpID;
      }
    },
    UPGRADE_WP: (state, action) => {
      const { ID, level } = action.payload;
      getItem(state.myWps, ID).level = level;
    },
    REFINE_WP: (state, action) => {
      const { ID, rfm } = action.payload;
      getItem(state.myWps, ID).refinement = rfm;
    },
    ADD_ART: (state, action) => {
      state.myArts.unshift(action.payload);
    },
    SORT_ARTS: (state) => {
      state.myArts.sort((a, b) => sortArtifacts(a, b));
    },
    SWAP_ART_USER: (state, action) => {
      const { newUser, artID, artType, oldUser } = action.payload;
      const { myChars, myArts } = state;
      getItem(myArts, artID).user = newUser;
      const { artIDs } = getChar(myChars, newUser);
      const pos = artTypes.indexOf(artType);
      if (artIDs[pos]) {
        getItem(myArts, artIDs[pos]).user = oldUser;
      }
      if (oldUser) {
        getChar(myChars, oldUser).artIDs[pos] = artIDs[pos];
      }
      artIDs[pos] = artID;
    },
    REMOVE_ART: (state, action) => {
      const { ID, user, type, setChosenID } = action.payload;
      const { myArts, myChars } = state;
      let index = myArts.findIndex((item) => item.ID === ID);
      myArts.splice(index, 1);
      if (myArts.length > 0) {
        if (index === myArts.length) index--;
        setChosenID(myArts[index].ID);
      }
      if (user) {
        const pos = artTypes.indexOf(type);
        getChar(myChars, user).artIDs[pos] = null;
      }
    },
    ENHANCE_ART: (state, action) => {
      const { ID, level } = action.payload;
      getItem(state.myArts, ID).level = level;
    },
    ENHANCE_ART_BY_4: (state, action) => {
      const { ID, rarity } = action.payload;
      const { myArts } = state;
      const art = getItem(myArts, ID);
      art.level = Math.min(rarity === 4 ? 16 : 20, art.level + 4);
    },
    ENHANCE_ART_TO_MAX: (state, action) => {
      const { ID, rarity } = action.payload;
      getItem(state.myArts, ID).level = rarity === 4 ? 16 : 20;
    },
    CHANGE_ART_MS_TYPE: (state, action) => {
      const { ID, type } = action.payload;
      getItem(state.myArts, ID).mainSType = type;
    },
    CHANGE_ART_SS_TYPE: (state, action) => {
      const { ID, ssIndex, type } = action.payload;
      for (const art of state.myArts) {
        if (art.ID === ID) {
          art.subS[ssIndex].type = type;
          break;
        }
      }
    },
    CHANGE_ART_SS_VAL: (state, action) => {
      const { ID, ssIndex, value } = action.payload;
      for (const art of state.myArts) {
        if (art.ID === ID) {
          const subS = art.subS[ssIndex];
          const processedValue = processNumInp(value, subS.val, 9999);
          subS.val = processedValue;
          break;
        }
      }
    },
    OVERWRITE_ART: (state, action) => {
      const { ID, ...stats } = action.payload;
      const { myArts } = state;
      const index = myArts.findIndex((art) => art.ID === ID);
      myArts[index] = { ...myArts[index], ...stats };
    }
  }
});

export const {
  UPLOAD_DB,
  ADD_CHAR,
  SORT_CHARS,
  TOGGLE_CONS,
  CHANGE_TALENT,
  CHANGE_CHAR_LEVEL,
  REMOVE_CHAR,
  SWITCH_WP,
  SWITCH_ART,
  UNEQUIP_ART,
  ADD_WP,
  SORT_WPS,
  SWAP_WP_USER,
  REMOVE_WP,
  UPGRADE_WP,
  REFINE_WP,
  ADD_ART,
  SORT_ARTS,
  SWAP_ART_USER,
  REMOVE_ART,
  ENHANCE_ART,
  ENHANCE_ART_BY_4,
  ENHANCE_ART_TO_MAX,
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  OVERWRITE_ART
} = databaseSlice.actions;

export default databaseSlice.reducer;
