import { useState } from "react";
import { useSelector } from "react-redux";
import { Button, Select } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import CharSorting from "./CharSorting";

export default function TopBarSmall(props) {
  const { myChars } = useSelector((state) => state.database);
  return (
    <Box className="py-3 flex" bgColor="cardBg2">
      {myChars.length ? (
        <CharSelect {...props} />
      ) : (
        <Button
          className="mx-auto"
          variant="positive"
          onClick={() => props.setPickerOn(true)}
        >
          Add New Characters
        </Button>
      )}
    </Box>
  );
}

function CharSelect({ charIndex, setCharIndex, setPickerOn }) {
  const [sorting, setSorting] = useState(false);
  const { myChars } = useSelector((state) => state.database);
  return (
    <div className="full-w flex-center relative">
      <Select
        className="px-2 py-1"
        color="black"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.5rem"
        fontWeight={700}
        align="center"
        value={myChars[charIndex].name}
        onChange={(e) =>
          setCharIndex(myChars.findIndex(({ name }) => name === e.target.value))
        }
      >
        {myChars.map(({ name }, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      <Button
        className="ml-4"
        variant="positive"
        onClick={() => setPickerOn(true)}
      >
        Add
      </Button>
      {sorting && <CharSorting close={() => setSorting(false)} />}
    </div>
  );
}
