import { useState } from "react";
import { Text, LGreen } from "../../../../styledCpns/DataDisplay";
import { Checkbox } from "../../../../styledCpns/Inputs";
import { Box, AbsoluteBox } from "../../../../styledCpns/Layout";
import { InfoSign, HowToModal } from "../../../CommonCpns";

export default function Configs({ configs, setConfigs }) {
  const [infoOn, setInfoOn] = useState(false);
  const options = [
    { field: "sepCharInfo", desc: "Separate Character's Info on each Setups" }
  ];
  return (
    <Box className="mt-4 p-3 relative" borderRadius="0.5rem" bgColor="cardBg2">
      <AbsoluteBox
        top="0.75rem"
        right="0.75rem"
        onClick={() => setInfoOn(true)}
      >
        <InfoSign selfHover={true} />
      </AbsoluteBox>
      <Text variant="h5" color="orange">
        Configurations
      </Text>
      {options.map(({ field, desc }, i) => (
        <div key={i} className={(i ? "mt-2" : "mt-1") + " flex align-center"}>
          <Checkbox
            scale={1.8}
            className="ml-1 mr-3"
            checked={configs[field]}
            onChange={() =>
              setConfigs((prev) => ({ ...prev, [field]: !prev[field] }))
            }
          />
          <Text>{desc}</Text>
        </div>
      ))}
      {infoOn && (
        <HowToModal
          content={
            <div>
              <Text>
                - Be careful when the Calculator is under the effect of{" "}
                <LGreen>Separate Character's Info</LGreen> (level,
                constellation, talents) on each Setup. It can make things
                complicated.
              </Text>
              <Text className="mt-2">
                - When Separate Character's Info is deactivated. Info on the
                Standard Setup will be used for others.
              </Text>
              <Text className="mt-2">
                - Separate Character's Info will be reset at the start of every
                session.
              </Text>
            </div>
          }
          close={() => setInfoOn(false)}
        />
      )}
    </Box>
  );
}
