import { Green, LGreen, Red } from "../../styledCpns/DataDisplay";
import Version1 from "./Version1";
import Version2 from "./Version2";
import PurpleOnly from "./PurpleOnly";
import { getObjByCode } from "../misc";

const artifactBuffs = {
  32: [
    // Echoes of an Offering
    {
      desc: ({ ATTRs }) => {
        const { lines } = getObjByCode(Version2, 32).setBnes[1];
        return (
          <>
            {lines[0]} {lines[1]}{" "}
            <Red>
              Normal Attack DMG Bonus (base):{" "}
              {artifactBuffs[32][0].bnValue(ATTRs)}.
            </Red>
          </>
        );
      },
      affect: "self",
      addFinalBnes: ({ ATTRs, bonuses }) => {
        bonuses.NA.Flat += artifactBuffs[32][0].bnValue(ATTRs);
      },
      bnValue: (ATTRs) => Math.round(ATTRs.ATK * 0.7)
    }
  ],
  31: [
    // Vermillion Hereafter
    {
      desc: () => getObjByCode(Version2, 31).setBnes[1].buff,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [4],
      startsAt0: [true],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["ATK%"] += 10 * inputs[0] + 8;
      }
    }
  ],
  2: [
    // "Shimenawa's Reminiscence"
    {
      desc: () => getObjByCode(Version2, 2).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 50;
        bonuses.CA.DMG += 50;
        bonuses.PA.DMG += 50;
      }
    }
  ],
  3: [
    // Husk of Opulent Dreams
    {
      desc: () => (
        <>
          Curiosity can stack up to <Green>4</Green> <LGreen>times</LGreen>,
          each providing <Green>6%</Green> <LGreen>DEF</LGreen> and a{" "}
          <Green>6%</Green> <LGreen>Geo DMG Bonus</LGreen>.
        </>
      ),
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [4],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["DEF%"] += 6 * inputs[0];
        ATTRs["Geo DMG Bonus"] += 6 * inputs[0];
      }
    }
  ],
  5: [
    // Noblesse Oblige
    {
      desc: () => getObjByCode(Version1, 5).setBnes[1].desc,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["ATK%"] += 20;
      }
    }
  ],
  6: [
    // Bloodstained Chivalry
    {
      desc: () => getObjByCode(Version1, 6).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.CA.DMG += 50;
      }
    }
  ],
  18: [
    // Thundersoother
    {
      desc: () => getObjByCode(Version1, 18).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  7: [
    // Crimson Witch of Flame
    {
      desc: () => getObjByCode(Version1, 7).setBnes[1].buff,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [3],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["Pyro DMG Bonus"] += 7.5 * inputs[0];
      }
    }
  ],
  8: [
    // Lavawalker
    {
      desc: () => getObjByCode(Version1, 8).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  9: [
    // Archaic Petra
    {
      desc: () => getObjByCode(Version1, 9).setBnes[1].desc,
      affect: "party",
      labels: ["Element"],
      inputTypes: ["crystallize"],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs[inputs[0] + " DMG Bonus"] += 35;
      }
    }
  ],
  10: [
    // Retracing Bolide
    {
      desc: () => getObjByCode(Version1, 10).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 40;
        bonuses.CA.DMG += 40;
      }
    }
  ],
  19: [
    // Blizzard Strayer
    {
      desc: () => (
        <>
          When a character attacks an opponent affected by Cryo, their{" "}
          <LGreen>CRIT Rate</LGreen> is increased by <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 20;
      }
    },
    {
      desc: () => (
        <>
          If the opponent is Frozen, <LGreen>CRIT Rate</LGreen> is increased by
          an additional <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 20;
      }
    }
  ],
  20: [
    // Heart of Depth
    {
      desc: () => getObjByCode(Version1, 20).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 30;
        bonuses.CA.DMG += 30;
      }
    }
  ],
  11: [
    // Pale Flame
    {
      desc: () => getObjByCode(Version1, 11).setBnes[1].desc,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [2],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["ATK%"] += 9 * inputs[0];
        if (+inputs[0] === 2) ATTRs["Physical DMG Bonus"] += 25;
      }
    }
  ],
  12: [
    // Tenacity of the Millelith
    {
      desc: () => getObjByCode(Version1, 12).setBnes[1].desc,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["ATK%"] += 20;
        ATTRs["Shield Strength"] += 30;
      }
    }
  ],
  23: [
    // Berserker
    {
      desc: () => getObjByCode(PurpleOnly, 23).setBnes[1].desc,
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 24;
      }
    }
  ],
  24: [
    // Instructor
    {
      desc: () => getObjByCode(PurpleOnly, 24).setBnes[1].desc,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["Elemental Mastery"] += 120;
      }
    }
  ],
  27: [
    // Brave Heart
    {
      desc: () => getObjByCode(PurpleOnly, 27).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.AllDmg += 30;
      }
    }
  ],
  28: [
    // Martial Artist
    {
      desc: () => getObjByCode(PurpleOnly, 28).setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 25;
        bonuses.CA.DMG += 25;
      }
    }
  ]
};

export default artifactBuffs;
